import nebulas from 'nebulas';
import NebPay from 'nebpay.js';
const nebPay = new NebPay();
let neb = new nebulas.Neb();

const nebServerUrl = process.env.NEB_SERVER_URL
// const dappAddress = process.env.DAPP_ADDR

const dappAddress = 'n1qTsLz9Y6yqt48s8hYNzgPE8GT5uDxoVwC'



export default {
  dappAddress,
  nebServerUrl,

  /**
   * 查询
   * @param fcn
   * @param args
   * @param callback
   */
  query: function (fcn, args, callback) {
    const Account = nebulas.Account;
    // let neb = new nebulas.Neb();
    neb.setRequest(new nebulas.HttpRequest(nebServerUrl));
    args = this.getCallArgs(args)
    const from = Account.NewAccount().getAddressString();
    const value = '0';
    const nonce = '0';
    const gas_price = '1000000';
    const gas_limit = '2000000';
    const contract = {
      function: fcn,
      args,
    };

    neb.api.call(from, dappAddress, value, nonce, gas_price, gas_limit, contract).then((resp) => {
      console.log('response of query:', JSON.stringify(resp));
      let result = resp.result;  // string
      console.log(args, result);
      let err = resp.execute_err;
      if (err && err != 'insufficient balance')
        return callback(err)

      callback(null, result)

    }).catch((err) => {
      console.log(`Error ${fcn} :${err.message}`);
      callback(err.message)
    });
  },

  callQueryPayInfo(callFunction, callArgs, value, callback) {

    const to = dappAddress;
    value = value?value:'0';
    const callbackUrl =  process.env.NODE_ENV == 'development' ? NebPay.config.testnetUrl: NebPay.config.mainnetUrl;
    console.log('callbackurl ', callbackUrl);
    const options = {
      callback: callbackUrl,
    }

    // 将数组转成string
    callArgs = this.getCallArgs(callArgs)

    const serialNumber = nebPay.call(to, value, callFunction, callArgs, options);

    let that = this
    let count = 5 //最多查询5次
    this.timer = setInterval(function () {
      console.log('=======Begin query Pay Info #', count);
      count--;
      if(count<0){
        clearInterval(that.timer)
        callback('未知状态')
      }

      that.queryPayInfo(serialNumber, (err) => {
        clearInterval(that.timer)
        if(err) {
          // console.log('err', err);
          if (callback)
            return callback(err)
          else
            return
        }
        if (callback)
          callback(null)
      })
    }, 10000)
  },

  call(callFunction, callArgs, value) {

    const nebPay = new NebPay();
    const to = dappAddress;
    value = value?value:'0';
    callArgs = this.getCallArgs(callArgs)
    const callbackUrl =  process.env.NODE_ENV == 'development' ? NebPay.config.testnetUrl: NebPay.config.mainnetUrl;
    console.log('callbackurl ', callbackUrl);
    const options = {
      callback: callbackUrl,
    }
    const serialNumber = nebPay.call(to, value, callFunction, callArgs, options);

    return serialNumber;
  },

  queryPayInfo(serialNumber, callback) {
    const nebPay = new NebPay();
    nebPay.queryPayInfo(serialNumber)   //search transaction result from server (result upload to server by app)
      .then(function (resp) {
        console.log("tx result: " + resp)   //resp is a JSON string
        var respObject = JSON.parse(resp)
        if(respObject.code === 0){
          let data = respObject.data
          if (data.status == 0){ // 失败
            // alert(data.execute_error)
            console.log('失败');
            callback(data.execute_error)
          } else  if (data.status == 1){// 成功

            console.log('=========== 成功, 调转到首页');
            callback(null)


          } else if (data.status == 2){
            console.log('待定');
          }
        }
      })
      .catch(function (err) {
        console.log(err);
        callback(err)
      });
  },


  /**
   * 入参可以是数组或字符串
   * @param list
   * @return {string}
   */
  getCallArgs(list) {
    if (list.constructor !== Array){
      // console.log(list, 'is not array');
      list = [list]
    }
    // console.log('list ', list);
    let str = '['
    list.forEach((value, index) =>{
      str += '"' + value + '",'
    })
    str = str.slice(0, str.length -1)
    str+=']'
    return str
  }

}





