import Vue from 'vue'

const userKey = 'user'

export default {

  user() {
    return Vue.$utilslib.storageGet(userKey)
  },
  setUser(user) {
    Vue.$utilslib.storageSet(userKey, user)
  },
  isLogin() {
    return this.user()?true:false;
  },

  login(username, password, callback) {
    // console.log('userlib.login', username, password);
    // let args = Vue.$neblib.getCallArgs([username, password])
    Vue.$neblib.query('login', [username, password], (err) => {
      if (err) {
        // return console.log('err #', err);
       return callback(err)
      }

      this.setUser({
        username: username
      })
      callback(null)
      // console.log(result);
    })
  },

  register(username, password, callback) {
    console.log('userlib. register');

    let value = 0
    Vue.$neblib.callQueryPayInfo('register', [username, password], value, (err) => {
      if(err){

        return callback(err)
      }

      this.setUser({
        username: username
      })

      callback(null)
    })
  },

  logout() {
    this.setUser(null)
  },

}



